import { yupResolver } from '@hookform/resolvers/yup'
import { Button, Checkbox, Stack, TextField, Typography } from '@mui/material'
import { WebAuthorAPI } from 'API/author'
import DialogConfirm from 'components/DialogConfirm'
import { IAccount, PERMISSION } from 'interfaces'
import { useEffect, useMemo, useState } from 'react'
import { SubmitHandler, useForm } from 'react-hook-form'
import { MdOutlinePersonRemove } from 'react-icons/md'
import { useNavigate } from 'react-router-dom'
import { useAppDispatch, useAppSelector } from 'redux/hooks'
import { getGroups } from 'redux/slices/Group'
import { UIAction } from 'redux/slices/UI'
import * as yup from 'yup'

interface FormInput {
	email: string
}

export default function Create() {
	const nav = useNavigate()
	const [loading, setLoading] = useState(false)
	const [open, setOpen] = useState(true)
	const [search, setSearch] = useState('')
	const dispatch = useAppDispatch()
	const status = useAppSelector((s) => s.Group.status)
	useEffect(() => {
		if (status === 'init') dispatch(getGroups())
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [])
	const account = useAppSelector((s) =>
		s.Group.list.filter((f) => f.id !== 1 && f.permission[PERMISSION.CMS] > 4)
	).reduce((p, c) => [...p, ...c.accounts], [] as IAccount[])
	const canSL = useMemo(() => {
		return account.filter((f) => f.email.includes(search) || f.name.includes(search))
	}, [account, search])

	const schema = yup.object({
		email: yup.string().required().email(),
	})
	const {
		register,
		handleSubmit,
		formState: { errors },
	} = useForm<FormInput>({
		resolver: yupResolver(schema),
		defaultValues: {
			email: '',
		},
	})

	const [selectedAccount, setSelectedAccount] = useState<IAccount[]>([])

	useEffect(() => {
		if (!open) {
			nav(-1)
		}
		return () => {
			setOpen(false)
		}
	}, [nav, open])

	const onSubmit: SubmitHandler<FormInput> = async ({ email }) => {
		if (selectedAccount.length === 0) return
		try {
			setLoading(true)
			const { error, message } = await WebAuthorAPI.create({
				email,
				userIds: selectedAccount.map((v) => v.id),
			})
			dispatch(
				UIAction.addGlobalMessage({
					type: error ? 'error' : 'success',
					message: message,
				})
			)
			setOpen(false)
		} finally {
			setLoading(false)
		}
	}
	return (
		<DialogConfirm
			open={open}
			loading={loading}
			handleClose={() => setOpen(false)}
			title="承認メールアドレス登録"
			disabled={Object.keys(errors).length > 0 || selectedAccount.length === 0}
			primaryButtonText="登録"
			primaryButtonColor="success"
			component="form"
			maxWidth="md"
			handleSubmit={handleSubmit(onSubmit)}
		>
			<Stack gap={1}>
				<Stack
					gap={1}
					flex={1}
					sx={{
						border: '1px solid #ccc',
						p: 0.5,
					}}
				>
					<TextField
						variant="standard"
						autoFocus
						required
						margin="dense"
						fullWidth
						size="small"
						label={'メールアドレス'}
						placeholder="ex:xxx@caa.go.jp"
						InputLabelProps={{ shrink: true }}
						{...register('email')}
						error={'email' in errors}
					/>
				</Stack>
				<Stack
					gap={1}
					flex={1}
					sx={{
						border: '1px solid #ccc',
						p: 0.5,
					}}
				>
					<Stack sx={{ bgcolor: '#eee', p: 1, maxHeight: 300, overflow: 'auto' }} gap={1}>
						<Typography>選択した承認者</Typography>
						<Stack direction="row" flexWrap="wrap" gap={1}>
							{selectedAccount.map((v) => {
								return (
									<Stack
										key={v.id}
										direction="row"
										alignItems="center"
										sx={{
											border: '1px solid #ccc',
											px: 0.5,
										}}
									>
										<Typography variant="caption">{v.email}</Typography>
										<Button
											color="error"
											sx={{
												minWidth: 0,
											}}
											onClick={() => {
												const tmp = selectedAccount.filter((f) => f.id !== v.id)
												setSelectedAccount(tmp)
											}}
										>
											<MdOutlinePersonRemove />
										</Button>
									</Stack>
								)
							})}
						</Stack>
					</Stack>
					<Stack
						sx={{
							border: '1px solid #ccc',
							height: '50vh',
							overflow: 'auto',
							p: 0.5,
						}}
					>
						<Typography>承認可能者</Typography>
						<Stack p={0.5}>
							<TextField
								variant="outlined"
								margin="dense"
								fullWidth
								size="small"
								label={'フィルター'}
								InputLabelProps={{ shrink: true }}
								value={search}
								onChange={(e) => setSearch(e.target.value)}
							/>
						</Stack>
						{canSL.map((v) => {
							return (
								<Stack key={v.id} px={0.5}>
									<Stack direction="row" alignItems="center" gap={0.5}>
										<Checkbox
											size="small"
											sx={{
												p: 0,
											}}
											checked={!!selectedAccount.find((f) => f.id === v.id)}
											onChange={(e) => {
												const checked = e.target.checked
												if (checked) {
													setSelectedAccount([...selectedAccount, v])
												} else {
													const tmp = selectedAccount.filter((f) => f.id !== v.id)
													setSelectedAccount(tmp)
												}
											}}
										/>
										<Typography variant="caption">{v.email}</Typography>
									</Stack>
								</Stack>
							)
						})}
					</Stack>
				</Stack>
			</Stack>
		</DialogConfirm>
	)
}
